/* major breakpoints */
@custom-media --sp (max-width: 767px);
@custom-media --tb (min-width: 768px) and (max-width: 1023px);
@custom-media --tb-h (min-width: 1024px) and (max-width: 1279px);
@custom-media --not-sp (min-width: 768px);
:root {
  --app-height: 100%;
}

.copy {
  font-family: "IBM Plex Sans JP", sans-serif;
  font-weight: 700;
}

.cards .card:not(:nth-child(3n)) {
  width: calc(50% - 0.5rem);
}
.cards .card img {
  width: 100%;
}
.cards .card .caption {
  font-size: 15px;
}